import React,{Fragment} from "react"
import Webcam from "react-webcam";
import imgg from "./camera.png"
import { Camera } from 'react-cam';
export default function Footer(props){
  const [image,setImage]=React.useState('');

  const videoConstraints = {
    width:10,
    height: 10,
    facingMode: "user"
  }


  const webcamRef = React.useRef(null);
    const cam = React.useRef(null);
   function capture(imgSrc) {
     props.setimage(imgSrc)
   }
  return(
    <div  className={props.with==1 ? "prodsgroupwith" :"prodsgroup"}>
    {props.with==1 && <input type="text" name="code" onChange={props.ff} placeholder="code"  value={props.code}    className="main-input"/>}
    <Camera
      showFocus={true}
      front={false}
      capture={capture}
      ref={cam}
      width="300"
      height="300"
      btnColor="white"
          focusWidth="3%"
          focusHeight="3%"
    />
  {props.with==1 && <input type="number" name="numbertraps" onChange={props.ff} placeholder="number of traps"  value={props.numbertraps}    className="main-input"/>}
    <input type="number" name="number" onChange={props.ff} placeholder="number"  value={props.number}    className="main-input"/>
    <div>  <input type="number" name="price" placeholder="price" onChange={props.ff}  value={props.price} className="main-input"/><input type="text" onChange={(e)=>props.sets(e.target.value)} value={props.sym}  className="main-sym"/></div>
    <button className="button" onClick={props.add}>+Add</button>
    <div></div>
  {props.with==1 &&   <div></div>}
    <img onClick={img => cam.current.capture(img)} alt="" className="iconcamera" src={imgg}/>
    </div>
  )
}
