import React from "react"
import Ligne from "./components/Ligne"
import Header from "./components/header"
import {nanoid} from "nanoid"
import Footer from "./components/footer"
import Total from "./components/total"
import imgg from "./image2.png"
import Choice from "./components/choise"
export default function App(){
  function emptyprod(){
    return{
code:"",
img:imgg,
numbertraps:1,
number:'',
price:'',
id:nanoid()}
}
const [prods,setprods]=React.useState(JSON.parse(localStorage.getItem("prods")) || [])
const [some,setsome]=React.useState(JSON.parse(localStorage.getItem("some")))
  const [withee,setwithee]=React.useState(JSON.parse(localStorage.getItem("with")))
  const [symbole,setsymbole]=React.useState("Dhs")
  const [vis,setvis]=React.useState(true)
  const [prod,setprod]=React.useState(emptyprod())

  function ghayar(e){
  const {name,type,value} =e.target
setprod(prevv=>{
  return{...prevv,
  [name]:value
}
})
}

React.useEffect(() => {
  localStorage.setItem("prods", JSON.stringify(prods));
  localStorage.setItem("some", JSON.stringify(some));
},[prods]);

React.useEffect(() => {
  localStorage.setItem("with", JSON.stringify(withee));
},[withee]);

function restartl(){
  localStorage.setItem("prods", JSON.stringify([]));
  localStorage.setItem("some", JSON.stringify(0));
  console.log(withee);
  localStorage.setItem("with", JSON.stringify(0));
  console.log(withee);
  window.location.reload(false);
}


function setimg(img){
  setprod(v=>{
    return {
    ...v,
    img:img
  }})
}

function addtoprods(){
  setprods(v=>[...v,prod])
  setsome(v=>v+prod.price*prod.number*prod.numbertraps)
  setprod(v=>emptyprod())
}

function editprods(id){
    let j
  for(let i=0;i<prods.length;i++){
    if(prods[i].id===id){
      j=i
    console.log(prods[j].price+"         "+j);
    }
  }
    console.log(prods[j].price+"         "+j);
  setprod(prods[j])
  setsome(v=>v-prods[j].price*prods[j].number*prods[j].numbertraps)
  setprods(v=>v.filter(function(item) {
      return item.id !== id
  }))
}
const lignes=prods.map(v=><Ligne with={withee} visf={vis} key={v.id} btn={true} edit={ ()=>editprods(v.id)} sym={symbole} {...v}/>)
  return(
    <div>
    {lignes}
  {some!==0 && <Total some={some} sym={symbole}/>}
{ vis && <div><br/>
  <br/>
  { withee!==0 ? <div><Ligne with={withee} {...prod} sym={symbole} />
<Footer with={withee} ff={ghayar} setimage={setimg} {...prod} add={addtoprods} sets={setsymbole} sym={symbole} restart={restartl}/>
    <div className="prodsgroup2">
    <button className="button" onClick={function bb() {setvis(false); setTimeout(window.print,3000);}}>Download Pdf</button>
<button className="buttondg" onClick={restartl}>restart</button>  </div></div> : <Choice setw={setwithee}/>}</div>}
      </div>
  )
}
