import React from "react"

export default function Ligne(props){

  return(
    <div className={props.with==1 ? "prodsgroupwith" :"prodsgroup"}>
{props.with===1 &&   <h3>{props.code}</h3>}
    <img src={props.img} alt="" className="prodimg" />
    {props.with===1 && <h3>{props.numbertraps} traps</h3>}
    <h3>{props.number} ps</h3>
    <h3> {props.price} {props.sym}</h3>
    <h3> {props.numbertraps*props.price*props.number} {props.sym}</h3>
  { props.visf && (props.btn && <button className="buttondg" onClick={props.edit}>Edit</button>)}
    </div>
  )
}
